import React, { useState, useEffect, useContext, useRef } from 'react';
import Circuit from './Circuit';
import getKey from '../utils/KeyGenerator';
import CollapseToggleButton from '../utils/CollapseToggleButton';
import { CircuitContext } from '../context/CircuitContext';
import CircuitGroupNavigation from './CircuitGroupNavigation';
import CircuitGroupEditTitle from './CircuitGroupEditTitle';
import initCircuitTracking from './initCircuitTracking';
import initializeData from './initializeData';
import validate from '../utils/validate';

const CircuitGroup = ({
    circuitGroup,
    circuitGroupIndex,
    deleteGroup,
    addCircuit
}) => {

    const {
        circuits,
        setCircuits,
        sort,
        targetElementRef,
        scrollToBottom
    } = useContext(CircuitContext);

    const [collapsed, setCollapsed] = useState();
    const [edit, setEdit] = useState(false);
    const isEditedCircuitGroupTitle = () => (edit) ? true : false;
    const [editedCircuitGroupTitle, setEditedCircuitGroupTitle] = useState(null);
    const [cursorPosition, setCursorPosition] = useState(0);
    const textareaRef = useRef(null);

    const focusTextarea = () => {
        if (textareaRef.current) {
            textareaRef.current.focus();
            textareaRef.current.setSelectionRange(cursorPosition, cursorPosition);
        }
    };
    useEffect(() => {
        focusTextarea();
    }, [editedCircuitGroupTitle, cursorPosition]);
    useEffect(() => {
        let dataUpdated = false;
        const updatedCircuits = [...circuits];
        const selectedNewCircuit = updatedCircuits[circuitGroupIndex];
        //if (selectedNewCircuit.circuits === undefined) {
        if (validate(selectedNewCircuit.circuits) === null) {
            selectedNewCircuit.circuits = [];
            dataUpdated = true;
        }
        if (dataUpdated) {
            setCircuits(updatedCircuits);
        }
        //console.log(`CircuitGroup => circuitGroup: ${circuitGroup.title}.isCollapsed: ${circuitGroup.isCollapsed}`);
        //console.log(`CircuitGroup => selectedNewCircuit: ${selectedNewCircuit.title}.isCollapsed: ${selectedNewCircuit.title.isCollapsed}`);
        setCollapsed(circuitGroup.isCollapsed || false);
    }, []);
    
    useEffect(() => {
        const updatedCircuits = [...circuits];        
        const selectedCircuitGroup = updatedCircuits[circuitGroupIndex];
        updatedCircuits[circuitGroupIndex].isCollapsed = collapsed;
        //updatedCircuits[circuitGroupIndex].circuits.map((circuit, index) => circuit.isCollapsed = collapsed)
        //setCircuits(updatedCircuits);
        localStorage.setItem('circuitTracking', JSON.stringify(updatedCircuits))
        console.log(`localStorage.setItem('circuitTracking')1`)
        //console.log(`setCollapsed => updatedCircuits: ${JSON.stringify(updatedCircuits, null, 2)}`);
        console.log(`CircuitGroup => ${updatedCircuits[circuitGroupIndex].title} isCollapsed: ${updatedCircuits[circuitGroupIndex].isCollapsed}`);
        
    }, [collapsed]);
    
    /* 
    useEffect(() => {
        const newCircuits = [...circuits];
        const updatedCircuits = newCircuits.map((circuitGroup, groupIndex) => {
            //if (groupIndex === circuitGroupIndex) {
            return {
                ...circuitGroup,
                isCollapsed: collapsed,
                circuits: circuitGroup.circuits.map((circuit) => ({
                    ...circuit,
                    isCollapsed: collapsed,
                }))
            };
            //}
            //return circuitGroup;
        });
        //console.log(`setCollapsed => updatedCircuits: ${updatedCircuits.map((circuitGroup, groupIndex) => circuitGroup.circuits.map((circuit) => (circuit.isCollapsed)))}`);
        localStorage.setItem('circuitTracking', JSON.stringify(updatedCircuits));
        //console.log(`setCollapsed => updatedCircuits: ${JSON.stringify(updatedCircuits, null, 2)}`);
    }, [collapsed]);
    */
    const toggleEdit = () => {
        const toggle = (edit)
            ? false
            : true;
        const wasCircuitGroupTitleEdited = (circuitGroup.title !== editedCircuitGroupTitle) ? true : false;
        setEdit(toggle);
        setEditedCircuitGroupTitle((toggle) ? circuitGroup.title : '');
        if (!toggle && wasCircuitGroupTitleEdited) {
            const updatedCircuits = [...circuits];
            const selectedNewCircuitGroup = updatedCircuits[circuitGroupIndex];
            selectedNewCircuitGroup.title = (wasCircuitGroupTitleEdited) ? editedCircuitGroupTitle : selectedNewCircuitGroup.title;
            setCircuits(updatedCircuits);
        }
    }
    const handleChange = (e) => {
        setEditedCircuitGroupTitle(e.target.value)
        setCursorPosition(e.target.selectionStart);
    };
    const addToGroup = (circuitGroupIndex, elementRef) => {
        addCircuit(circuitGroupIndex)
        scrollToBottom(elementRef);
    }
    return <div key={getKey(`circuit${circuitGroupIndex}`)} className='' ref={targetElementRef}>
        <div className=''>
            <div className='containerBox flexContainer bg-lite'>
                <div className='flex1Auto'>
                    {
                        (isEditedCircuitGroupTitle())
                        ? <CircuitGroupEditTitle
                            circuitGroup={circuitGroup}
                            circuitGroupIndex={circuitGroupIndex}
                            textareaRef={textareaRef}
                            handleChange={handleChange}
                            editedCircuitGroupTitle={editedCircuitGroupTitle}
                            toggleEdit={toggleEdit}
                        />
                        : <div className='containerBox bg-lite centerVertical p-20 color-yellow bold size25'>
                            <CollapseToggleButton
                                title={circuitGroup.title}
                                isCollapsed={collapsed}
                                setCollapse={setCollapsed}
                                align='left'
                                editTitle={toggleEdit}
                            />
                        </div>
                    }
                </div>
            </div>
            {
                (collapsed)
                ? null
                : <CircuitGroupNavigation
                    circuitGroupIndex={circuitGroupIndex}
                    addToGroup={addToGroup}
                    deleteGroup={deleteGroup}
                />
            }
            <div>
                {
                    (collapsed)
                    ? null
                    : (sort)
                        ? circuitGroup.circuits.map((circuit, circuitIndex, array) => (
                            <div key={getKey(`circuitContainer${circuitIndex}`)}>
                                <Circuit
                                    circuitGroupIndex={circuitGroupIndex}
                                    circuitIndex={circuitIndex}
                                    circuit={circuit}
                                />
                            </div>
                        ))
                        : circuitGroup.circuits.map((circuit, circuitIndex) => (
                            <div key={getKey(`circuitContainer${circuitIndex}`)}>
                                <Circuit
                                    circuitGroupIndex={circuitGroupIndex}
                                    circuitIndex={circuitIndex}
                                    circuit={circuit}
                                />
                            </div>
                        ))
                }
            </div>
        </div>
    </div>
}
export default CircuitGroup;