import React, { useState, useEffect } from 'react';
import getKey from './utils/KeyGenerator';
import initializeData from './utils/InitializeData';
import CollapseToggleButton from './utils/CollapseToggleButton';
import icons from './site/icons';

const StepManager = () => {
  const [stepSchedules, setSchedules] = useState(JSON.parse(localStorage.getItem('stepSchedules')) || []);
  const [steps, setSteps] = useState(JSON.parse(localStorage.getItem('steps')) || []);
  const [scheduleIndex, setScheduleIndex] = useState(initializeData('schedule', 0));
  const [currentSchedule, setCurrentSchedule] = useState(stepSchedules[scheduleIndex] || null);
  const [currentStepIndex, setCurrentStepIndex] = useState(initializeData('currentStep', 0));
  const [currentTimer, setCurrentTimer] = useState(0);
  const [isRunning, setIsRunning] = useState(false);
  const [wasRunning, setWasRunning] = useState(false);
  const [newScheduleName, setNewScheduleName] = useState('');
  const [newStepDescription, setNewStepDescription] = useState('');
  const [newStepDuration, setNewStepDuration] = useState({ hours: 0, minutes: 0, seconds: 0 });
  const [newStepNote, setNewStepNote] = useState('');
  const [note, setNote] = useState('');
  const [collapseAdd, setCollapseAdd] = useState(false);
  const [stepsCollapsed, setStepsCollapsed] = useState(true);
  const [addStepCollapsed, setAddStepCollapsed] = useState(true);

  const formatTimestamp = (timestamp) => {
    if (!timestamp) return 'completed';

    const date = new Date(timestamp);

    let hours = date.getHours();
    const minutes = date.getMinutes().toString().padStart(2, '0');
    const seconds = date.getSeconds().toString().padStart(2, '0');
    const period = hours >= 12 ? 'pm' : 'am';
    hours = hours % 12 || 12;

    return `${hours}:${minutes}:${seconds} ${period}`;
  };
  const updatedSteps = () => {
    if (stepSchedules && stepSchedules[scheduleIndex] && stepSchedules[scheduleIndex].steps) {
      const now = Date.now(); // Get current timestamp in milliseconds
      const steps = [...stepSchedules[scheduleIndex].steps] || [];
      console.log(`StepManager => updatedSteps => steps: ${JSON.stringify(steps, null, 2)}`);

      // Calculate time offset for past steps
      const elapsedTime = steps
        .slice(0, currentStepIndex)
        .reduce((total, step) => total + step.duration * 1000, 0);

      console.log(`updatedSteps => currentStepIndex: ${currentStepIndex} elapsedTime: ${elapsedTime}`);

      let accumulatedTime = now - elapsedTime; // Adjust start time for past steps

      return steps.map((step, index) => {
        const startTime = accumulatedTime;
        const endTime = startTime + step.duration * 1000;
        accumulatedTime = endTime; // Move accumulated time forward

        return {
          ...step,
          startTime: formatTimestamp(startTime),
          endTime: formatTimestamp(endTime)
        };
      });
    }
  }

  useEffect(() => {
    if (stepSchedules.length > 0) {
      setCurrentSchedule(stepSchedules[scheduleIndex]);
      setSteps(updatedSteps());
    }
  }, [scheduleIndex]);
  useEffect(() => {
    if (stepSchedules.length > 0) {
      setCurrentSchedule(stepSchedules[scheduleIndex]);
      console.log(`StepManager => updatedSteps(): ${JSON.stringify(updatedSteps(), null, 2)}`);
      setSteps(updatedSteps())
    }
  }, [stepSchedules]);

  useEffect(() => {
    const storedState = JSON.parse(localStorage.getItem('stepManagerState'));
    if (storedState) {
      const { lastTime, scheduleName, stepIndex, timeLeft, running } = storedState;
      const elapsedTime = Math.floor((Date.now() - lastTime) / 1000);
      const selectedSchedule = stepSchedules.find(schedule => schedule.name === scheduleName);
      if (selectedSchedule) {
        setCurrentSchedule(selectedSchedule);
        let updatedStepIndex = Number(stepIndex);
        let updatedTimeLeft = timeLeft - elapsedTime;
        while (updatedTimeLeft <= 0 && selectedSchedule.steps && updatedStepIndex < selectedSchedule.steps.length - 1) {
          updatedStepIndex += 1;
          console.log(`StepManager => updatedStepIndex: ${updatedStepIndex}`);
          updatedTimeLeft += selectedSchedule.steps[updatedStepIndex].duration;
        }
        setCurrentStepIndex(updatedStepIndex);
        setCurrentTimer(updatedTimeLeft);
        setIsRunning(running);
      }
    }
    setSteps(updatedSteps());
  }, [stepSchedules]);

  useEffect(() => {
    if (isRunning) {
      const timer = setInterval(() => {
        setCurrentTimer(prev => prev - 1);
      }, 1000);
      return () => clearInterval(timer);
    }
  }, [isRunning]);
  useEffect(() => {
    const stateToStore = {
      lastTime: Date.now(),
      scheduleName: currentSchedule?.name,
      stepIndex: currentStepIndex,
      timeLeft: currentTimer,
      running: isRunning,
    };
    localStorage.setItem('stepManagerState', JSON.stringify(stateToStore));
    localStorage.setItem('currentStep', currentStepIndex);
    setSteps(updatedSteps())
  }, [currentStepIndex]);

  useEffect(() => {
    localStorage.setItem('stepSchedules', JSON.stringify(stepSchedules));
  }, [stepSchedules]);

  useEffect(() => {
    if (currentTimer <= 0 && isRunning) {
      handleNextStep();
    }
  }, [currentTimer, isRunning]);

useEffect(() => {
    const handleBeforeUnload = () => {
      const stateToStore = {
        lastTime: Date.now(),
        scheduleName: currentSchedule?.name,
        stepIndex: currentStepIndex,
        timeLeft: currentTimer,
        running: isRunning,
      };
      localStorage.setItem('stepManagerState', JSON.stringify(stateToStore));
    };
    window.addEventListener('beforeunload', handleBeforeUnload);
    return () => {
      handleBeforeUnload();
      window.removeEventListener('beforeunload', handleBeforeUnload);
    };
  }, [currentSchedule, currentStepIndex, currentTimer, isRunning]);

  const handleNextStep = () => {
    if (currentStepIndex < currentSchedule.steps.length - 1) {
      setCurrentStepIndex(currentStepIndex + 1);
      setCurrentTimer(currentSchedule.steps[currentStepIndex + 1].duration);
    } else {
      setCurrentStepIndex(0);
      setCurrentTimer(currentSchedule.steps[0].duration);
    }
  };
  const handlePreviousStep = () => {
    if (currentStepIndex > 0) {
      setCurrentStepIndex(currentStepIndex - 1);
      setCurrentTimer(currentSchedule.steps[currentStepIndex - 1].duration);
    } else {
      setIsRunning(false);
    }
  };
  const handleAddSchedule = () => {
    const newSchedule = {
      name: newScheduleName,
      steps: [],
    };
    setSchedules([...stepSchedules, newSchedule]);
    setNewScheduleName('');
  };

  const handleAddStep = () => {

    //console.log(`StepManager => handleAddStep => newStepDuration: ${JSON.stringify(newStepDuration, null, 2)}`);

    const durationInSeconds = newStepDuration.hours * 3600 + newStepDuration.minutes * 60 + newStepDuration.seconds;
    const newStep = {
      description: newStepDescription,
      duration: durationInSeconds,
      note: newStepNote,
    };
    const updatedSchedules = stepSchedules.map(schedule => {
      if (schedule.name === currentSchedule.name) {
        return {
          ...schedule,
          steps: [...schedule.steps, newStep],
        };
      }
      return schedule;
    });
    setSchedules(updatedSchedules);
    setNewStepDescription('');
    setNewStepDuration({ hours: 0, minutes: 0, seconds: 0 });
    setNewStepNote('');
  };

  const handleSelectSchedule = (e) => {
    if (String(e.target.value).includes('add')) {
      setCurrentSchedule(null);
    } else {
      const selectedScheduleIndex = parseInt(e.target.value, 10);
      setScheduleIndex(selectedScheduleIndex);
    }
  };

  const handleStart = () => setIsRunning(true);
  const handlePause = () => setIsRunning(false);
  const handleReset = () => {
    setCurrentStepIndex(0);
    setCurrentTimer(currentSchedule.steps[0]?.duration || 0);
    setIsRunning(false);
  };

  const handleNoteChange = (noteIndex, newNote) => {
    const newSchedules = [...stepSchedules];
    newSchedules[scheduleIndex].steps[noteIndex].note = newNote;
    setSchedules(newSchedules);
  };
  const handleStepChange = (stepIndex, newStep) => {
    const newSchedules = [...stepSchedules];
    newSchedules[scheduleIndex].steps[stepIndex].description = newStep;
    setSchedules(newSchedules);
  };
  const handleDurationChange = (index, newDuration) => {
    const newSchedules = [...stepSchedules];
    newSchedules[scheduleIndex].steps[index].duration = newDuration;
    setSchedules(newSchedules);
  };

  const handleSelectStep = (e) => {
    const selectedStepIndex = parseInt(e.target.value, 10);
    setCurrentStepIndex(selectedStepIndex);
    setCurrentTimer(currentSchedule.steps[selectedStepIndex].duration);
  };

  const handleMouseEnter = () => {
    setWasRunning(isRunning);
    setIsRunning(false);
  };

  const handleMouseLeave = () => {
    if (wasRunning) {
      setIsRunning(true);
    }
  };

  const handleTouchStart = () => {
    setWasRunning(isRunning);
    setIsRunning(false);
  };

  const handleTouchEnd = () => {
    if (wasRunning) {
      setIsRunning(true);
    }
  };

  const formatDuration = (durationInSeconds) => {
    const hours = Math.floor(durationInSeconds / 3600);
    const minutes = Math.floor((durationInSeconds % 3600) / 60);
    const seconds = durationInSeconds % 60;
    return `${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}:${String(seconds).padStart(2, '0')}`;
  };
  const setStepDescription = (index, description) => {
    const newSchedules = [...stepSchedules];
    const newStep = {
      description: description,
      duration: 0,
      note: ''
    }

    if (newSchedules[scheduleIndex].steps[index]) {
      console.log(`StepManager => setStepDescription => step: ${JSON.stringify(newSchedules[scheduleIndex].steps[index].description, null, 2)}`);
      newSchedules[scheduleIndex].steps[index].description = description;
    } else {
      newSchedules[scheduleIndex].steps.push(newStep)
    }
    setSchedules(newSchedules);
  }
  const setStepHours = (index, hours) => {
    const newSchedules = [...stepSchedules];
    const durationHours = Number(formatDuration(newSchedules[scheduleIndex].steps[index].duration).split(':')[0])
    const durationMinutes = Number(formatDuration(newSchedules[scheduleIndex].steps[index].duration).split(':')[1])
    const durationSeconds = Number(formatDuration(newSchedules[scheduleIndex].steps[index].duration).split(':')[2])
    const durationInSeconds = hours * 3600 + durationMinutes * 60 + durationSeconds;
    newSchedules[scheduleIndex].steps[index].duration = durationInSeconds;
    setSchedules(newSchedules);
  }
  const setStepMinutes = (index, minutes) => {
    const newSchedules = [...stepSchedules];
    const durationHours = Number(formatDuration(newSchedules[scheduleIndex].steps[index].duration).split(':')[0])
    const durationMinutes = Number(formatDuration(newSchedules[scheduleIndex].steps[index].duration).split(':')[1])
    const durationSeconds = Number(formatDuration(newSchedules[scheduleIndex].steps[index].duration).split(':')[2])
    const durationInSeconds = durationHours * 3600 + minutes * 60 + durationSeconds;
    newSchedules[scheduleIndex].steps[index].duration = durationInSeconds;
    setSchedules(newSchedules);
  }
  const setStepSeconds = (index, seconds) => {
    const newSchedules = [...stepSchedules];
    const durationHours = Number(formatDuration(newSchedules[scheduleIndex].steps[index].duration).split(':')[0])
    const durationMinutes = Number(formatDuration(newSchedules[scheduleIndex].steps[index].duration).split(':')[1])
    const durationSeconds = Number(formatDuration(newSchedules[scheduleIndex].steps[index].duration).split(':')[2])
    const durationInSeconds = durationHours * 3600 + durationMinutes * 60 + seconds;
    newSchedules[scheduleIndex].steps[index].duration = durationInSeconds;
    setSchedules(newSchedules);
  }
  const removeStep = (index) => {
    const newSteps = [...stepSchedules[scheduleIndex].steps.slice(0, index), ...stepSchedules[scheduleIndex].steps.slice(index + 1)];
    const newSchedules = [...stepSchedules];
    newSchedules[scheduleIndex].steps = [...newSteps];
    console.log(`removeStep => newSchedules: ${JSON.stringify(newSchedules, null, 2)}`);
    setSchedules(newSchedules);
  }
  const getNewStep = (type, index, step) => <div className='containerBox'>
    {
      (type !== 'add')
        ? <div className='containerBox flexContainer'>
          <div className='flex2Column centeredContent columnLeftAlign pl-10'>
            Step {index + 1}
          </div>
          <div className='flex2Column columnRightAlign'>
            <div
              title='remove step'
              className='w-50 rt-10 r-5 size15 bg-lite color-yellow button pr-20 pl-20 pt-10 pb-10'
              onClick={() => removeStep(index)}
            >
              X
            </div>
          </div>
        </div>
        : null
    }
    <div className='containerBox'>
      {
        (type === 'add')
        ? <input
          id='description'
          name='description'
          className='containerBox width--10 bg-dark'
          type='text'
          value={newStepDescription}
          onChange={(e) => setNewStepDescription(e.target.value)}
          placeholder='Step Description'
        />
        : <input
          id='description'
          name='description'
          className='containerBox width--10 color-yellow bold bg-dark'
          type='text'
          value={step.description}
          onChange={(e) => setStepDescription(index, e.target.value)}
          placeholder='Step Description'
        />
      }
    </div>
    {`${steps[index]?.startTime} - ${steps[index]?.endTime}`}
    <div className='containerBox flexContainer'>
      <div className='flex2Column color-yellow columnRightAlign pr-5'>
        Hours:
      </div>
      {
        (type === 'add')
          ? <input
            id='hours'
            name='hours'
            className='flex2Column columnLeftAlign bg-dark color-lite width-50-percent'
            type='number'
            value={newStepDuration.hours}
            onChange={(e) => setNewStepDuration({ ...newStepDuration, hours: e.target.value })}
            placeholder='Hours'
          />
          : <input
            id='hours'
            name='hours'
            className='flex2Column columnLeftAlign bg-dark color-lite width-50-percent'
            type='number'
            value={Number(formatDuration(step.duration).split(':')[0])}
            onChange={(e) => setStepHours(index, e.target.value)}
            placeholder='Hours'
          />
      }
    </div>
    <div className='containerBox flexContainer'>
      <div className='flex2Column color-yellow columnRightAlign pr-5'>
        Minutes:
      </div>
      {
        (type === 'add')
          ? <input
            id='minutes'
            name='minutes'
            className='flex2Column columnLeftAlign bg-dark color-lite width-50-percent'
            type='number'
            value={newStepDuration.minutes}
            onChange={(e) => setNewStepDuration({ ...newStepDuration, minutes: e.target.value })}
            placeholder='Minutes'
          />
          : <input
            id='minutes'
            name='minutes'
            className='flex2Column columnLeftAlign bg-dark color-lite width-50-percent'
            type='number'
            value={Number(formatDuration(step.duration).split(':')[1])}
            onChange={(e) => setStepMinutes(index, e.target.value)}
            placeholder='Minutes'
          />
      }
    </div>
    <div className='containerBox flexContainer'>
      <div className='flex2Column color-yellow columnRightAlign pr-5'>
        Seconds:
      </div>
      {
        (type === 'add')
          ? <input
            id='seconds'
            name='seconds'
            className='flex2Column columnLeftAlign bg-dark color-lite width-50-percent'
            type='number'
            value={newStepDuration.seconds}
            onChange={(e) => setNewStepDuration({ ...newStepDuration, seconds: e.target.value })}
            placeholder='Seconds'
          />
          : <input
            id='seconds'
            name='seconds'
            className='flex2Column columnLeftAlign bg-dark color-lite width-50-percent'
            type='number'
            value={Number(formatDuration(step.duration).split(':')[2])}
            onChange={(e) => setStepSeconds(index, e.target.value)}
            placeholder='Seconds'
          />
      }
    </div>
    <div className='containerBox flexContainer'>
      <div className='flex2Column color-yellow columnRightAlign pr-5'>
        Notes:
      </div>
      {
        (type === 'add')
          ? <input
            id='notes'
            name='notes'
            className='containerBox columnLeftAlign width--10'
            type='text'
            placeholder='Add notes...'
            value={newStepNote}
            onChange={(e) => setNewStepNote(e.target.value)}
          />
          : <input
            id='notes'
            name='notes'
            className='containerBox columnLeftAlign width--10'
            type='text'
            placeholder='Add notes...'
            value={step.note}
            onChange={(e) => handleNoteChange(index, e.target.value)}
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
            onTouchStart={handleTouchStart}
            onTouchEnd={handleTouchEnd}
          />
      }
    </div>
    {
      (type === 'add')
        ? <div title={`${(type === 'add') ? 'Add' : 'Edit'} Step`} className='containerBox button bg-green' onClick={(type === 'add') ? handleAddStep : handleStepChange}>
            {(type === 'add') ? 'Add' : 'Edit'} Step
          </div>
        : null

    }
  </div>

  return (
    <div className='containerBox'>
      <div className='containerBox color-yellow bold'>
        Step Schedules:
      </div>
      <div key={getKey('StepSchedule')} className='containerBox bg-lite color-yellow'>
        <div className='containerBox'>
          <select
            title='select schedule'
            className='containerBox button width-100-percent'
            value={scheduleIndex}
            onChange={handleSelectSchedule}
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
            onTouchStart={handleTouchStart}
            onTouchEnd={handleTouchEnd}
          >
            {stepSchedules.map((schedule, index) => (
              <option className='containerBox' key={getKey(schedule.name)} value={index}>
                {schedule.name}
              </option>
            ))}
            <option className='containerBox' value='add'>Add Schedule</option>
          </select>
        </div>
        {currentSchedule && (
          <div className='containerBox'>
            <div className='containerBox bg-lite'>
              <select
                value={currentStepIndex}
                onChange={handleSelectStep}
                onMouseEnter={handleMouseEnter}
                onMouseLeave={handleMouseLeave}
                onTouchStart={handleTouchStart}
                onTouchEnd={handleTouchEnd}
                className='containerDetail m-5  button width--10 color-yellow bold size40 p-10 columnCenterAlign'
              >
                {
                  (!currentSchedule.steps)
                  ? null
                  : currentSchedule.steps.map((step, index) => (
                      <option key={index} value={index}>{step.description}</option>
                    ))
                }
              </select>
              <div className='containerDetail m-5 p-30 bg-tintedMedium color-yellow bold size40'>
                {formatDuration(currentTimer)}
              </div>
              <div className='containerBox color-yellow'>
                  {
                    (steps && steps[currentStepIndex] && steps[currentStepIndex].startTime && steps[currentStepIndex].endTime)
                    ? `${steps[currentStepIndex].startTime} - ${steps[currentStepIndex].endTime}`
                    : null
                  }
              </div>
              <div className='containerBox flexContainer'>
                <div className='flex2Column columnRightAlign color-yellow m-10'>
                  Duration:
                </div>
                <div className='flex2Column columnLeftAlign m-10'>
                  {
                    (!currentSchedule.steps)
                    ? null
                    : formatDuration(currentSchedule.steps[currentStepIndex]?.duration)}
                </div>
              </div>
              <div className='containerBox'>
                {
                  (!currentSchedule.steps)
                  ? null
                  : currentSchedule.steps[currentStepIndex]?.note
                }
              </div>
            </div>
            <div className='containerBox flexContainer'>
              <div className='flex3Column navButton-container columnLeftAlign'>
                <div className='button size40' onClick={handlePreviousStep}>
                  {icons.leftArrow}
                </div>
              </div>
              <div className='flex3Column'>
                <div className='navButton-container'>
                  <div className='button size40' onClick={handleStart}>
                    {icons.play}
                  </div>
                  <div className='button size40' onClick={handlePause}>
                    {icons.pause}
                  </div>
                  <div className='button size40' onClick={handleReset}>
                    {icons.restart}
                  </div>
                </div>
              </div>
              <div className='flex3Column navButton-container columnRightAlign'>
                <div className='button size40' onClick={handleNextStep}>
                  {icons.rightArrow}
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
      {currentSchedule && (
        <div>
          <div className='containerBox'>
            <CollapseToggleButton
              title={<span className='color-yellow bold size30'>Steps</span>}
              isCollapsed={stepsCollapsed}
              setCollapse={setStepsCollapsed}
              align='center'
            />
          </div>
          {
            (stepsCollapsed || !currentSchedule.steps)
            ? null
            : currentSchedule.steps.map((step, index) => (
              (!step.description)
              ? null
              : <div key={getKey(`${step.description}`)} className={`containerBox color-yellow bold p-20 size30 ${(index === currentStepIndex)?'bg-dkYellow':'bg-lite'}`}>
                {
                  (stepSchedules[scheduleIndex].steps[index])
                  ? getNewStep('edit', index, step)
                  : null
                }
              </div>
            ))
          }
        </div>
      )}
      {currentSchedule === null && (
        <div>
          <h2>Add Schedule</h2>
          <input
            id='schedule'
            name='schedule'
            type='text'
            value={newScheduleName}
            onChange={(e) => setNewScheduleName(e.target.value)}
            placeholder='Schedule Name'
          />
          <button onClick={handleAddSchedule}>Add Schedule</button>
        </div>
      )}
      {currentSchedule && (
        <div>
          <div className='containerBox'>
            <CollapseToggleButton
              title={<span className='color-yellow bold size30'>Add Step</span>}
              isCollapsed={addStepCollapsed}
              setCollapse={setAddStepCollapsed}
              align='center'
            />
          </div>
          {
            (addStepCollapsed)
            ? null
            : getNewStep('add', currentSchedule.steps.length, null)
          }
        </div>
      )}
    </div>
  );
};

export default StepManager;