import React, { useEffect, useState } from 'react';
import initRecipeTracking from './initRecipeTracking';
import RecipeGroup from './RecipeGroup';
import getKey from '../utils/KeyGenerator';
import initializeData from '../utils/InitializeData';
import CollapseToggleButton from '../utils/CollapseToggleButton';
import Ingredient from './Ingredient';

const TrackRecipe = ({
    recipes,
    setRecipes,
    targetElementRef,
    scrollToBottom
}) => {

    const [ingredientsCollapse, setIngredientsCollapse] = useState(true);

    useEffect(() => {
        if ((recipes === null) && (initializeData('recipeTracking', null) === null)){
            setRecipes(initRecipeTracking);
        }
        localStorage.setItem('recipeTracking', JSON.stringify(recipes));
    }, [recipes]);

    useEffect(() => {
        const storedRecipes = (recipes !== null) ? recipes : initializeData('recipeTracking', initRecipeTracking);
        setRecipes(storedRecipes);
    }, []);

    const notNull = (value) => (value !== null) ? true : false;
    const notEmpty = (value) => (value !== "") ? true : false;
    const isGood = (value) => (notNull(value) && notEmpty(value)) ? true : false;

    const addRecipe = (recipeGroupIndex, recipeIndex) => {
        const updatedRecipes = [...recipes];
        const recipeDescription = prompt('Dish name:', '');
        const newRecipe = {
            dish: recipeDescription,
            //recipe: recipe
            description: ''
        }
        if (isGood(recipeDescription)/* || isGood(recipe)*/) {
            updatedRecipes[recipeGroupIndex].recipes.push(newRecipe)
            setRecipes(updatedRecipes);
        }

    };
    const subtractRecipe = (recipeGroupIndex, recipeIndex) => {
        const updatedRecipes = [...recipes];
        const recipe = updatedRecipes[recipeGroupIndex].recipes[recipeIndex];
        recipe.sessions = recipe.sessions ?? [];
        recipe.isRunning = false;
        recipe.sessions.pop();
        setRecipes(updatedRecipes);
    };

    const deleteGroup = (recipeGroupIndex) => {
        const toggle = window.confirm(`Are you sure you want to remove recipe group ${recipes[recipeGroupIndex].category}`)
        const removeItemByIndex = (array, index) => {
            if (index >= 0 && index < array.length) {
                array.splice(index, 1);
            } else {
                console.error("Index out of range");
            }
        };
        if (toggle) {
            const newRecipes = [...recipes];
            removeItemByIndex(newRecipes, recipeGroupIndex);
            setRecipes(newRecipes);
        }
    }
    const removeDuplicates = (array) => [...new Set(array)];
    const getAllIngredients = () => initializeData('ingredients', [['salt','salt', 'salt']]);
    const removePeeled = (ingredient) => ingredient.replace('peeled', '');
    const removeHalved = (ingredient) => ingredient.replace('halved', '');
    const removeSeeded = (ingredient) => ingredient.replace('seeded', '');
    const removeAnd = (ingredient) => ingredient.replace(' and ', '');
    const removeSliced = (ingredient) => ingredient.replace('sliced', '');
    const removeBruised = (ingredient) => ingredient.replace('bruised', '');
    const removeChopped = (ingredient) => ingredient.replace('chopped', '');
    const removeMinced = (ingredient) => ingredient.replace('minced', '');
    const removeComma = (ingredient) => ingredient.replace(',', '');
    const removeExtraStuff = (ingredient) => {
        const peeled = removePeeled(ingredient);
        const and = removeAnd(peeled);
        const sliced = removeSliced(and);
        const seeded = removeSeeded(sliced);
        const halved = removeHalved(seeded);
        const bruised = removeBruised(halved);
        const chopped = removeChopped(bruised);
        const minced = removeMinced(chopped);
        const comma = removeComma(minced);
        return comma.trim();
    }
    const ingredients = () => getAllIngredients().map(ingredient => (ingredient[2] !== '' && ingredient[2] !== undefined) ? ingredient[2].toLowerCase() : (ingredient[1] !== '' && ingredient[1] !== undefined) ? ingredient[1].toLowerCase() : (ingredient[0] !== '' && ingredient[0] !== undefined) ? ingredient[0].toLowerCase() : 'salt');
    const cleanedUpIngredients = () => ingredients.map(ingredient => removeExtraStuff(ingredient));
    const sterilIngredients = () => removeDuplicates(cleanedUpIngredients);

    return (
        <div key={getKey('recipeGroupContainer')}>
           {
           /*  
           <div className='containerBox color-yellow'>
                <CollapseToggleButton
                    title={'Ingredients'}
                    isCollapsed={ingredientsCollapse}
                    setCollapse={setIngredientsCollapse}
                    align='left'
                />
            </div> 
            */
            }
            {
                /* 
                (ingredientsCollapse)
                ? null
                : sterilIngredients.map(ingredient => <Ingredient 
                                                        ingredient={ingredient}
                                                        />
                ) 
                */
            }
            {recipes.map((recipeGroup, recipeGroupIndex) => (
                <div key={getKey('recipeGroups')}>
                    <RecipeGroup
                        recipes={recipes}
                        setRecipes={setRecipes}
                        recipeGroup={recipeGroup}
                        recipeGroupIndex={recipeGroupIndex}
                        deleteGroup={deleteGroup}
                        addRecipe={addRecipe}
                        //setScroll={setScroll}
                        targetElementRef={targetElementRef}
                        scrollToBottom={scrollToBottom}
                    />
                </div>
            ))}
        </div>
    )
}

export default TrackRecipe