import React, { useState, useEffect } from 'react';
import useOceanData from './useOceanData';
import CollapseToggleButton from '../utils/CollapseToggleButton';
import icons from '../site/icons';
import validate from '../utils/validate';
import initializeData from '../utils/InitializeData';
//import UserManagement from './UserManagement';
//import TodoApp from './TodoApp';

const Sunset = ({ isMotionOn }) => {

    const latitude = initializeData('latitude', null);
    const longitude = initializeData('longitude', null);

    const matchCoordinateLengths = () => {
        const lonStr = longitude.toString();
        const latStr = latitude.toString();
        const lonLength = lonStr.length;
        const latLength = latStr.length;
        if (lonLength === latLength) {
            return { longitude, latitude };
        }
        let trimmedLongitude = lonStr;
        let trimmedLatitude = latStr;
        if (lonLength > latLength) {
            const excess = lonLength - latLength;
            trimmedLongitude = lonStr.slice(0, -excess);
        } else if (latLength > lonLength) {
            const excess = latLength - lonLength;
            trimmedLatitude = latStr.slice(0, -excess);
        }
        return [parseFloat(trimmedLongitude), parseFloat(trimmedLatitude)];
    }

    // Example usage
    const result = matchCoordinateLengths();
    //console.log(`Sunset => result: ${result}`);

    const sunset1uri = `https://api.sunrise-sunset.org/json?lat=${matchCoordinateLengths()[1]}&lng=${matchCoordinateLengths()[0]}`;
    //console.log(`Sunset => sunset1uri: ${sunset1uri}`);
    //const sunset2uri = 'https://api.sunrise-sunset.org/json?lat=36.7201600&lng=-4.4203400&date=today';
    //const sunset3uri = 'https://api.sunrise-sunset.org/json?lat=36.7201600&lng=-4.4203400&date=2021-03-05';
    //const sunset4uri = 'https://api.sunrise-sunset.org/json?lat=36.7201600&lng=-4.4203400&formatted=0';
    const [sunrise, setSunrise] = useState(null);
    const [sunset, setSunset] = useState(null);
    const [darkTime, setDarkTime] = useState(null);
    // eslint-disable-next-line
    const [retry, setRetry] = useState('');
    const [data, setData] = useState(null);
    const sunsetData = useOceanData('sunset', sunset1uri, '', setRetry)
    const [surfTime, setSurfTime] = useState({ hours: 0, minutes: 0 });

    const getLocalData = (localItem) => initializeData(localItem, null);
    const collapseStateInit = (localItem) => getLocalData(localItem) ? getLocalData(localItem) === 'true' : true;
    const [surfTimeCollapse, setSurfTimeCollapse] = useState(collapseStateInit('surfTimeCollapse'));

    useEffect(() => {
        localStorage.setItem('surfTimeCollapse', surfTimeCollapse);
    }, [surfTimeCollapse]);
    useEffect(() => {
        //console.log(`Sunset => sunsetData: ${JSON.stringify(sunsetData, null, 2)}`);
        if (retry === 'complete' && sunsetData !== null) {
            setData(sunsetData);
        } else {
            const template = [{ "results": { "sunrise": "2:22:46 PM", "sunset": "12:46:17 AM", "solar_noon": "7:34:32 PM", "day_length": "10:23:31", "civil_twilight_begin": "1:57:41 PM", "civil_twilight_end": "1:11:23 AM", "nautical_twilight_begin": "1:27:36 PM", "nautical_twilight_end": "1:41:27 AM", "astronomical_twilight_begin": "12:58:04 PM", "astronomical_twilight_end": "2:10:59 AM" }, "status": "OK", "tzid": "UTC" }, null]
            const localData = initializeData('sunset', template);
            setData(localData);
        } 
    }, [retry]);

    useEffect(() => {
        const isFirstIndexValid = (data) => {
            // Check if data is an array with length > 0 and the first element is an object
            if (!Array.isArray(data) || data.length === 0 || typeof data[0] !== 'object' || data[0] === null) {
                return false; // Not a valid array or object
            }

            // Check if the first key of the object is 'results'
            const keys = Object.keys(data[0]);
            return keys.length > 0 && keys[0] === 'results';
        };
        if (isFirstIndexValid(sunsetData)) {
            //console.log(`${retry} Sunset => sunsetData: ${JSON.stringify(sunsetData[0])}`);
            //console.log(`${retry} Sunset => sunsetData: ${JSON.stringify(sunsetData, null, 2)}`);
            localStorage.setItem('sunset', JSON.stringify(sunsetData));
            setRetry('complete');
        }
    }, [sunsetData]);
    useEffect(() => {
        if (data !== null) {
            //console.log(`DDData: ${JSON.stringify(data,null,2)}`)
            const convertTimeToISOFormat = (time) => {
                const [timeStr, meridiem] = time.split(' ');
                const [hours, minutes, seconds] = timeStr.split(':').map(Number);
                let hours24 = hours % 12;
                if (meridiem === 'PM') {
                    hours24 += 12;
                }
                const currentTime = new Date();
                currentTime.setHours(hours24, minutes, seconds);
                const isoFormat = currentTime.toISOString();
                return isoFormat;
            }

            if (validate(data[0]) !== null && validate(data[0].results) !== null) {
                localStorage.setItem('sunset', JSON.stringify(data[0]));
                const sunsetTime = data[0].results.sunset;
                const sunsetTimeArray = sunsetTime.split(':');
                const sunsetHours = Number(sunsetTimeArray[0]) + 5;
                const sunsetMinutes = sunsetTimeArray[1];
                const sunsetDisplayTime = `${(sunsetHours > 12) ? (sunsetHours - 12) : sunsetHours}:${sunsetMinutes}`
                setSunset(sunsetDisplayTime);
                const sunriseTime = data[0].results.sunrise;
                const sunriseTimeArray = sunriseTime.split(':');
                const sunriseHours = ((Number(sunriseTimeArray[0]) + 5)>12)?((Number(sunriseTimeArray[0]) + 5)-12) : (Number(sunriseTimeArray[0]) + 5);
                const sunriseMinutes = sunriseTimeArray[1];
                const sunriseDisplayTime = `${sunriseHours}:${sunriseMinutes}`
                setSunrise(sunriseDisplayTime);
                const untilDark = data[0].results.civil_twilight_end;
                const untilDarkTimeArray = untilDark.split(':');
                const untilDarkHours = Number(untilDarkTimeArray[0]) + 5;
                const untilDarkMinutes = untilDarkTimeArray[1];
                const untilDarkDisplayTime = `${untilDarkHours}:${untilDarkMinutes}`
                setDarkTime(untilDarkDisplayTime);
                const currentTime = new Date().toString();
                const currentTimeObj = new Date(currentTime);
                const sunsetTimeObj = new Date(convertTimeToISOFormat(sunsetTime));
                const timeDifference = sunsetTimeObj.getTime() - currentTimeObj.getTime();
                const hoursRemaining = 17 + Math.floor(timeDifference / (1000 * 60 * 60));
                const minutesRemaining = 84 + Math.floor((timeDifference % (1000 * 60 * 60)) / (1000 * 60));
                const displayHours = (minutesRemaining > 60) ? (hoursRemaining + 1) : hoursRemaining;
                const displayMinutes = (minutesRemaining > 60) ? (minutesRemaining - 60) : minutesRemaining;

                setSurfTime({ hours: displayHours, minutes: displayMinutes });
            }
        }
    }, [data]);
    
    const daylightHeader = () => {
        if (retry !== 'complete') {
            return <div>
                {icons.bright} DAYLIGHT: Error fetching data retry attempt {retry}
            </div>
        }
        return <div>
            {/*{icons.bright} DAYLIGHT {surfTime.hours}<span className='size12'>hrs</span> {surfTime.minutes}<span className='size12'>min</span>*/}
            {icons.bright} DAYLIGHT {(surfTime.hours < 0) ? surfTime.hours + 1 : surfTime.hours}<span className='size12'>hrs</span> {surfTime.minutes}<span className='size12'>min</span>
        </div>
    }
    const getCurrentSunset = () => <React.Fragment>
        <div className='containerBox color-yellow size25 bg-lite size15 bold p-20'>
            <CollapseToggleButton
                title=''
                component={daylightHeader()}
                isCollapsed={surfTimeCollapse}
                setCollapse={setSurfTimeCollapse}
                align='left'
            />
        </div>
        {
            (surfTimeCollapse)
                ? <div></div>
                : <div className='containerBox'>
                    <div className='white p-10'>
                        {/*{surfTime.hours} hours {surfTime.minutes} minutes*/}
                        {(surfTime.hours < 0) ? surfTime.hours + 1 : surfTime.hours} hours {surfTime.minutes} minutes
                    </div>
                    <div className='flexContainer'>
                        <div className='flex3Column containerDetail'>
                            <div className='containerDetail p-10 bg-lite color-yellow bold size20'>
                                SUNRISE
                            </div>
                            <div className='p-10'>
                                {sunrise}
                                <span className='copyright bold'>
                                    AM
                                </span>
                            </div>
                        </div>
                        <div className='flex3Column containerDetail'>
                            <div className='containerDetail p-10 bg-lite color-yellow bold size20'>
                                SUNSET
                            </div>
                            <div className='p-10'>
                                {sunset}
                                <span className='copyright bold'>
                                    PM
                                </span>
                            </div>
                        </div>
                        <div className='flex3Column containerDetail'>
                            <div className='containerDetail p-10 bg-lite color-yellow bold size20'>
                                DARK
                            </div>
                            {/*<div className='copyright bold'>in {darkCount()}</div>*/}
                            <div className='p-10'>
                                {darkTime}
                                <span className='copyright bold'>
                                    PM
                                </span>
                            </div>
                        </div>
                    </div>
                    {/*<UserManagement/>*/}
                    {/*<TodoApp/>*/}
                </div>
        }
    </React.Fragment>;

    return getCurrentSunset();
}

export default Sunset;