import React, { useState, useEffect, useContext } from 'react';
import CircuitGroup from './CircuitGroup';
import getKey from '../utils/KeyGenerator';
import CollapseToggleButton from '../utils/CollapseToggleButton';
import { CircuitContext } from '../context/CircuitContext';
import initCircuitTracking from './initCircuitTracking';
import initializeData from './initializeData';
import icons from '../site/icons';

const TrackCircuit = (
    isCollapsed
) => {

    const {
        circuits,
        setCircuits,
        sort,
        setSort,
        activeIndex,
        setActiveIndex,
        setActivated,
        ticker,
        setTicker,
        countdown,
        setCountdown,
        jumpToActive,
        videoId,
        videoActive,
        edit,
        setEdit
    } = useContext(CircuitContext);

    const [, forceUpdate] = useState();
    const [collapse, setCollapse] = useState(isCollapsed);
    const [initialized, setInitialized] = useState(false);
    const notNull = (value) => (value !== null) ? true : false;
    const notEmpty = (value) => (value !== "") ? true : false;
    const isGood = (value) => (notNull(value) && notEmpty(value)) ? true : false;

    useEffect(() => {
        console.log(`TrackCircuit => circuits update`);
        //forceUpdate(Math.random());
    }, [circuits]);
    useEffect(() => {
    }, [activeIndex]);
    useEffect(() => {
        console.log(`TrackCircuit => videoActive: ${videoActive} videoId: ${videoId}`);
    }, [videoId]);
    
    useEffect(() => {
        const newCircuits = [...circuits];
        newCircuits.map((circuitGroup) => {
                return {
                    ...circuitGroup,
                    isCollapsed: collapse,
                    circuits: circuitGroup.circuits.map((circuit) => {
                        circuit.isCollapsed = collapse
                    })
                }
        });
        //console.log(`Circuits => newCircuits: ${JSON.stringify(newCircuits, null, 2)}`);
        localStorage.setItem('circuitTracking', JSON.stringify(newCircuits));
        setCircuits(newCircuits);
    }, [collapse]);
    
    useEffect(() => {
        if (initialized) {
            const updatedTrackingData = [...circuits];
            updatedTrackingData.map((group, groupIndex) => group.isCollapsed = collapse);
            setCircuits(updatedTrackingData);
        } else {
            setInitialized(true);
        }
    }, [collapse]);
    const toggleEdit = () => {
        setEdit(!edit);
    }
    const addCircuit = (circuitGroupIndex, circuitIndex) => {
        const updatedCircuits = [...circuits];
        const circuit = prompt('Circuit name:', '');
        const description = 'Set the Timer: Determine the duration for each exercise, usually around 30 seconds to 1 minute. Arrange the exercises in a sequence that allows you to transition smoothly from one exercise to the next. Consider alternating between upper body and lower body exercises to give specific muscle groups time to rest.';

        const newCircuit = {
            title: circuit,
            description: description,
            time: 5,
            restTime: 5,
            excersizes: [],
            isCollapsed: false
        }
        if (isGood(circuit)) {
            updatedCircuits[circuitGroupIndex].circuits.push(newCircuit)
            setCircuits(updatedCircuits);
        }

    };

    const deleteGroup = (circuitGroupIndex) => {
        const toggle = window.confirm(`Are you sure you want to remove circuit group ${circuits[circuitGroupIndex].title}`)
        const removeItemByIndex = (array, index) => {
            if (index >= 0 && index < array.length) {
                array.splice(index, 1);
            } else {
                console.error("Index out of range");
            }
        };
        if (toggle) {
            //const updatedCircuits = [...circuits];
            const updatedCircuits = [...circuits];
            removeItemByIndex(updatedCircuits, circuitGroupIndex);
            setCircuits(updatedCircuits);
        }
    }
    const reset = () => {
        const newCircuits = [...circuits];
        console.log(`newCircuits: ${JSON.stringify(newCircuits, null, 2)}`);
        newCircuits.map((circuitGroup) => {
            return {
                ...circuitGroup,
                circuits: circuitGroup.circuits.map((circuit) => {
                    return {
                        ...circuit,
                        isCollapsed: collapse
                    };
                })
            };
        });
        newCircuits.map((circuitGroup) => {
            return {
                ...circuitGroup,
                circuits: circuitGroup.circuits.map((circuit) => {
                    return {
                        ...circuit,
                        excersizes: circuit.excersizes.map((excersize) => {
                            return {
                                ...excersize,
                                complete: false,
                                activated: false,
                                currentTime: circuit.time,
                                restTime: circuit.restTime,
                                elapsedTime: 0
                            };
                        })
                    };
                })
            };
        });
        setCircuits(newCircuits);
        setActivated(false);
        setActiveIndex(null)
    }
    const tickerToggleButton = () => {
        return <div
            title='timer bell'
            className='containerBox bg-lite centerVertical p-20 color-yellow button scrollSnapRight'
            onClick={() => setTicker(!ticker)}
        >
            {icons.track} {(ticker) ? icons.soundOn : icons.soundOff}
        </div>
    }
    const countdownToggleButton = () => {
        return <div
            title='countdown bell'
            className='containerBox bg-lite centerVertical p-20 color-yellow button scrollSnapRight'
            onClick={() => setCountdown(!countdown)}
        >
            {icons.alarmOn} {(countdown) ? icons.soundOn : icons.soundOff}
        </div>
    }
    const resetButton = () => {
        return (
            <div 
                title='reset' 
                onClick={() => reset()} 
                className='containerDetail m-5 bg-lite centerVertical p-20 size12 bold button scrollSnapRight'
            >
                Reset
            </div>
        )
    }
    const collapseButton = () => {
        return (
            <div 
                title={(collapse) ? 'Expand All' : 'Collapse All'}
                onClick={() => setCollapse(!collapse)} 
                className='containerDetail m-5 bg-lite centerVertical p-20 size12 bold button scrollSnapRight'
            >
                {(collapse) ? 'Expand All' : 'Collapse All'}
            </div>
        )
    }
    const jumpToActiveButton = () => {
        return (
            <div 
                title='scroll to current excersize'
                onClick={() => jumpToActive()} 
                className='containerDetail m-5 bg-lite centerVertical p-20 size12 bold button scrollSnapRight'
            >
                Current Excersize
            </div>
        )
    }
    const editButton = () => {
        return (
            <div 
                title='edit'
                onClick={() => toggleEdit()} 
                className='containerDetail m-5 bg-lite centerVertical p-20 size12 bold button scrollSnapRight'
            >
                {icons.edit}
            </div>
        )
    }

    return (
        <div key={getKey('circuitGroupContainer')} className='pb-100'>
            {
                circuits.map((circuitGroup, circuitGroupIndex) => (
                        <div key={getKey('circuitGroups')}>
                            <CircuitGroup
                                circuitGroup={circuitGroup}
                                circuitGroupIndex={circuitGroupIndex}
                                deleteGroup={deleteGroup}
                                addCircuit={addCircuit}
                            />
                        </div>
                    ))
            }
            <div className={`containerDetail bt-5 ml-5 width--10 bg-tintedMediumDark size12 color-lite`}>
                <div className={`button-container`}>
                    <div title='current excersize' className='scrollSnapRight'>
                        {jumpToActiveButton()}
                    </div>
                    <div title='expand/collapse' className='scrollSnapRight'>
                        {collapseButton()}
                    </div>
                    <div title='reset' className='scrollSnapRight'>
                        {resetButton()}
                    </div>
                    <div title='edit' className='scrollSnapRight'>
                        {editButton()}
                    </div>
                    {tickerToggleButton()}
                    {countdownToggleButton()}
                </div>
            </div>
        </div>
    )
}

export default React.memo(TrackCircuit)